/**
 * Transition Roadmap Page JavaScript
 * 
 * This file contains the JavaScript for the Transition Roadmap page.
 */

// Import components
import Accordion from '../components/accordion';
import Tabs from '../components/tabs';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Transition Roadmap page initialized');
  
  // Initialize accordion
  Accordion.init({
    triggerSelector: '.roadmap-step-header',
    contentSelector: '.roadmap-step-content',
    closeOthers: false,
    animateHeight: true
  });
  
  // Initialize tabs
  Tabs.init({
    tabsContainerSelector: '.tabs-container',
    tabButtonSelector: '.tab-button',
    tabPanelSelector: '.tab-panel',
    activeClass: 'active'
  });
  
  // Set up progress tracking
  setupProgressTracking();
}

/**
 * Set up progress tracking
 */
function setupProgressTracking() {
  const progressItems = document.querySelectorAll('.progress-item');
  const progressCheckboxes = document.querySelectorAll('.progress-checkbox');
  
  if (progressCheckboxes.length > 0) {
    // Load saved progress
    loadProgress();
    
    // Add event listeners to checkboxes
    progressCheckboxes.forEach(checkbox => {
      checkbox.addEventListener('change', function() {
        saveProgress();
        updateProgressDisplay();
      });
    });
    
    // Update progress display
    updateProgressDisplay();
  }
}

/**
 * Save progress to localStorage
 */
function saveProgress() {
  const progressCheckboxes = document.querySelectorAll('.progress-checkbox');
  const progress = {};
  
  progressCheckboxes.forEach(checkbox => {
    const id = checkbox.getAttribute('id');
    progress[id] = checkbox.checked;
  });
  
  localStorage.setItem('transitionRoadmapProgress', JSON.stringify(progress));
}

/**
 * Load progress from localStorage
 */
function loadProgress() {
  const savedProgress = localStorage.getItem('transitionRoadmapProgress');
  
  if (savedProgress) {
    const progress = JSON.parse(savedProgress);
    const progressCheckboxes = document.querySelectorAll('.progress-checkbox');
    
    progressCheckboxes.forEach(checkbox => {
      const id = checkbox.getAttribute('id');
      if (progress[id] !== undefined) {
        checkbox.checked = progress[id];
      }
    });
  }
}

/**
 * Update progress display
 */
function updateProgressDisplay() {
  const progressItems = document.querySelectorAll('.progress-item');
  const progressCheckboxes = document.querySelectorAll('.progress-checkbox');
  const totalItems = progressCheckboxes.length;
  let completedItems = 0;
  
  progressCheckboxes.forEach(checkbox => {
    if (checkbox.checked) {
      completedItems++;
    }
  });
  
  const progressPercentage = totalItems > 0 ? Math.round((completedItems / totalItems) * 100) : 0;
  const progressBar = document.querySelector('.progress-bar');
  const progressText = document.querySelector('.progress-text');
  
  if (progressBar) {
    progressBar.style.width = `${progressPercentage}%`;
  }
  
  if (progressText) {
    progressText.textContent = `${completedItems} of ${totalItems} tasks completed (${progressPercentage}%)`;
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
